import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import CardProduct from "./../../../component/CardProduct/CardProduct";
import Pagination from "./../../../component/Pagination/Pagination";
import "./BodyProductList.scss";

BodyProductList.propTypes = {
  noItemPage: PropTypes.number,
  idCategory: PropTypes.string,
};
BodyProductList.defaultProps = {
  noItemPage: 12,
  idCategory:'',
};

function BodyProductList(props) {
  const { noItemPage, idCategory } = props;
  const [productList, setProductList] = useState([]);
  const [isShowLoading, setIsShowLoading] = useState(false);
  const [totalRow, setTotalRow] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);

  function selectedProduct(product) {
    console.log("product selected", product);
  }
  
  function getProductListByPagination(pagination) {
    const { currentPage } = pagination;
    setCurrentPage(currentPage);
  }
  // console.log("body",selectedCategories);
  useEffect(() => {
    setIsShowLoading(true);
    getProductList(idCategory, noItemPage, currentPage).then((response) => {
      setProductList([...response.data]);
      setTotalRow(response.pagination.totalRow);
      setIsShowLoading(false);
    });
  }, [idCategory, currentPage]);
  return (
    <div className="BodyProductList">
      <div className="ProductList">
        {isShowLoading && (
          <div className="ContainerLoading">
            <div className="Loading"></div>
            <img
              className="ImgLoading"
              src={require("../../../assets/images/loading.gif").default}
            />
          </div>
        )}
        {productList.map((product, index) => {
          return (
            <CardProduct
              key={index}
              addCardClick={selectedProduct}
              itemProduct={product}
            />
          );
        })}
      </div>
      <div className="Pagination">
        <Pagination
          pageChangeClick={getProductListByPagination}
          itemPage={noItemPage}
          totalItem={totalRow}
        />
      </div>
    </div>
  );
}

const getProductList = async (categoryId = null, _limit = 10, _page = 1) => {
  const uri = "https://api-json-server-290621.herokuapp.com/api/products";
  const headers = {
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
    },
  };
  let url =`${uri}?_limit=${_limit}&_page=${_page}`;
  if(categoryId){
      url = `${uri}?categoryId=${categoryId}&_limit=${_limit}&_page=${_page}`;
  }
 
  const response = await fetch(url, headers);
  const responseJson = await response.json();
  return responseJson;
};
export default BodyProductList;
