import React from "react";
import VerticalMenu from "../../component/VerticalMenu/VerticalMenu";
import Header from "../../component/Header/Header";
import NavigateQuickly from "../../component/NavigateQuickly/NavigateQuickly";
import { useLocation } from "react-router-dom";
import "./Product.scss";
import BodyProductList from "./BodyProductList/BodyProductList";
function Product() {
  function selectedCategory(item) {
    console.log(item);
  }
  const { state: { category: { id = "" } = {} } = {} } = useLocation();
  return (
    <div className="Product">
      <div className="Left">
        <VerticalMenu
          title="Sản phẩm"
          categoryList={categoryList}
          selectedCategory={selectedCategory}
        />
      </div>
      <div className="Right">
        <div className="NavigateQuickly">
          <NavigateQuickly listQuickLink={listQuickLink} />
        </div>
        <div className="Header">
          <Header title="Sản phẩm" />
        </div>

        <BodyProductList idCategory={id} />
      </div>
    </div>
  );
}

export default Product;

const categoryList = [
  {
    id: "1",
    title: "Phim ảnh & máy ảnh",
  },
  {
    id: "2",
    title: "Hàng gia dụng",
  },
  {
    id: "3",
    title: "Điện thoại",
  },
];

const listQuickLink = [
  {
    id: "PQ01",
    label: "Trang chủ",
    path: "/",
  },
  {
    id: "PQ02",
    label: "Sản phẩm",
    path: "news",
  },
];
