import React from 'react';
import PropTypes from 'prop-types';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import  './HeaderPage.scss';
import { Link } from '@material-ui/core';
HeaderPage.propTypes = {
  
};

function HeaderPage(props) {
  return (
    <div className="HeaderPage">
      <div className="Hotline">
        <div>
          <FontAwesomeIcon className="IconPhone" icon="phone" />
          <span>Hotline: 0777 20 30 42</span>
        </div>
      </div>
      <div className="NavigateInfo">
        <div className="ContainerNav">
          <FontAwesomeIcon className="IconPhoneNav" icon="pen-square" />
          <Link className="Link">Kiểm tra đơn hàng</Link>
        </div>
        <div className="ContainerNav">
          <FontAwesomeIcon className="IconPhoneNav" icon="cart-plus" />
          <Link className="Link">Giỏ hàng</Link>
        </div>
        <div className="ContainerNav">
          <FontAwesomeIcon className="IconPhoneNav" icon="share-square" />
          <Link className="Link">Đăng nhập</Link>
        </div>
        <div className="ContainerNav">
          <FontAwesomeIcon className="IconPhoneNav" icon="key" />
          <Link className="Link">Đăng kí</Link>
        </div>
      </div>
    </div>
  );
}

export default HeaderPage;