import React from "react";
import Table from "../Table/Table";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Button } from "@material-ui/core";
import { useSelector } from "react-redux";
import "./Cart.scss";
function Cart(){
  let cartList = useSelector((carts) => carts.cart.cartList);
  cartList=calculateIntoMoney(cartList);
  return (
    <div className="Cart">
      <div className="Image">
        <img src={require("../../assets/images/cart.png").default} />
      </div>
      <span className="InfoCart">
        {calculateAmount(cartList)} sp - {calculateTotal(cartList)} <sup>đ</sup>
      </span>
      <FontAwesomeIcon icon="arrow-right" className="Icon" />
      <div className="InfoDetail">
        <div>
          <Table dataList={cartList} headerList={headerList} />
        </div>

        <div className="Total">
          <span className="Label">Tổng tiền:</span>
          <span className="Content">
            {calculateTotal(cartList)}
            <sup>đ</sup>
          </span>
        </div>
        <div className="ContainerButton">
          <Button className="ButtonPayment">Thanh toán</Button>
        </div>
      </div>
    </div>
  );
};

const calculateTotal = (cartList) => {
  let total = 0;
  cartList.forEach((cart) => {
    total +=((parseFloat(cart.price) * cart.discount) / 100) * parseInt(cart.amount);
  });
  return total;
};

const calculateIntoMoney = (cartList) => {
  return cartList.map(item=>{
    const total =((parseFloat(item.price) * item.discount) / 100) * parseInt(item.amount);
    return { ...item, intoMoney: total };
  });
};
const calculateAmount = (cartList) => {
  let totalAmount = 0;
  cartList.forEach((cart) => {
    totalAmount += cart.amount;
  });
  return totalAmount;
};

export default Cart;


const headerList = [
  {
    flex: 1,
    label: "",
    propertyMapping: "imageUrl",
    type: "IMAGE",
  },
  {
    flex: 3,
    label: "TÊN SẢN PHẨM",
    propertyMapping: "productName",
    type: "LABLE",
  },
  {
    flex: 2,
    label: "GIÁ",
    propertyMapping: "price",
    type: "LABLE",
  },
  {
    flex: 2,
    label: "SỐ LƯỢNG",
    propertyMapping: "amount",
    type: "LABLE",
  },
  {
    flex: 2,
    label: "THÀNH TIỀN",
    propertyMapping: "intoMoney",
    type: "LABLE",
  },
  {
    flex: 1,
    label: "",
    actionType: ["DELETE"],
    type: "ACTION",
  },
];
